/* 设置滚动条的样式 */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    background: rgba(255, 255, 255, 0);
}

/* 滚动槽 */
::-webkit-scrollbar-track {
    border-radius: 8px;
}

/* 滚动条滑块 */
::-webkit-scrollbar-thumb {
    border-radius: 8px;
    background: rgba(255, 255, 255, 0);
}

:hover::-webkit-scrollbar-thumb {
    border-radius: 8px;
    background: rgba(173, 226, 186, .5);
}

::-webkit-scrollbar-corner {
    background: rgba(255, 255, 255, 0);
}

/* 默认样式重置 */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
dl,
dd {
    margin: 0;
    font-family: "微软雅黑";
}

body {
    /*overflow-x: auto;*/
    /*overflow-y: hidden;*/
    background: #fff;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

td,
th {
    padding: 0;
}

ul,
ol {
    margin: 0;
    padding: 0;
    list-style: none;
}

main {
    display: block;
}

a {
    text-decoration: none;
    cursor: pointer;
}

mark {
    background-color: none;
}

img {
    vertical-align: top;
}

.ant-layout-header {
    height: 48px;
