.folder-icon {
    position: relative;
    background: url("../img/lg-bg.svg") no-repeat top center/100%;
}

.folder-icon img, .file-icon img{
    width: 100%;
    height: 100%;
}

.folder-icon-fg, .folder-icon-fg {
    width: 100%;
    position: relative;
}
.folder-icon-ct {
    position: absolute;
    width: 90%;
    height: 30px;
    background-color: #fff;
    margin: 12% 5%;
}
.file-icon .file-icon-ph {
    visibility: hidden;
}

.ant-card-body-title {
    width: 100%;
    display: inline-block;
    -ms-flex: 1;
    flex: 1 1;
    padding-top: 6px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.file-icon {
    /*font-size: 100px;*/
}

.code-content {
    background: #2d2d2d;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

pre[class*="language-"] {
    width: 100%;
    margin: 0;
    padding: .8em;
    overflow-wrap: inherit;
}

code[class*="language-"] {
    white-space: pre-wrap;
}

.md_container {
